import { Component } from '@angular/core';

import { Loan, LoanInfoDataConfig, LoanItemCustomBase } from '@backbase/loans-collection-ui-ang';

import { getLoanInfoDataConfig } from './loan-info-wrapper.config';
import { LoanDetailsBaseComponent } from '../../../base/loan-details/loan-details-base-component';

@Component({
  selector: 'bb-loan-info-wrapper',
  templateUrl: './loan-info-wrapper.component.html',
})
export class LoanInfoWrapperComponent extends LoanDetailsBaseComponent<LoanInfoDataConfig<Loan, LoanItemCustomBase>> {
  /**
   * Configuration for loan info container
   */
  getDataConfig({ viewType }: Loan): LoanInfoDataConfig<Loan, LoanItemCustomBase> {
    return getLoanInfoDataConfig(viewType);
  }
}
